'use strict';

angular.module('videoHub')
  .constant('SponsorDetailControllerConstants', {
    SAVED_ALERT_MESSAGE: 'Saved',
    SOMETHING_WENT_WRONG_ALERT_MESSAGE: 'Something went wrong with saving the Sponsor. Please call your friendly sysadmin.',
    PLEASE_FILL_IN_ALL_REQUIRED_FIELDS_ALERT_MESSAGE: 'Please fill in all required fields.'
  })
  .controller('SponsorDetailController', function($scope, $routeParams, SponsorService, $location, AlertService, SponsorDetailControllerConstants) {
    $scope.sponsor = {};

    $scope.getSponsor = function() {
      if (_.isUndefined($routeParams.sponsorId)) {
        $scope.sponsor = {};
      } else {
        return SponsorService.one($routeParams.sponsorId).get().then(
          function(data) {
            $scope.sponsor = data;
          },
          function(data) {
            console.log(data);
          }
        );
      }
    };

    $scope.saveSponsor = function() {
      if ($scope.sponsorDetailForm.$valid) {
        if ($routeParams.sponsorId) {
          $scope.sponsor.save().then(displaySaveSuccessfulAlert, displaySomethingWentWrongAlert);
        } else {
          SponsorService.post($scope.sponsor).then(navigateToNewlyCreatedEditPage, displaySomethingWentWrongAlert);
        }
      } else {
        AlertService.error(SponsorDetailControllerConstants.PLEASE_FILL_IN_ALL_REQUIRED_FIELDS_ALERT_MESSAGE, true);
      }
    };

    function navigateToNewlyCreatedEditPage(response) {
      displaySaveSuccessfulAlert().then(function() {
        $location.path('cms/editsponsor/' + response.id + '/');
      });
    }

    function displaySaveSuccessfulAlert() {
      return AlertService.success(SponsorDetailControllerConstants.SAVED_ALERT_MESSAGE, true);
    }

    function displaySomethingWentWrongAlert() {
      AlertService.error(SponsorDetailControllerConstants.SOMETHING_WENT_WRONG_ALERT_MESSAGE, true);
    }
    $scope.getSponsor();
  });
