'use strict';
angular.module('videoHub')
  .directive('autocomplete', function($injector) {
    return {
      templateUrl: 'shared/autocomplete/autocomplete.html',
      scope: {
        'modelId': '=',
        'service': '@service',
        'labelAttr': '@',
        'placeholderText': '@'
      },
      controller: function($scope) {
        //Inject service based on provided value
        var service = $injector.get($scope.service);
        //Debounce the search to avoid spamming requests
        $scope.$watch('search', function() {
          if ($scope.modelSelected || $scope.showDropdown || _.isEmpty($scope.search)) {
            return;
          } else {
            service.getList().then(updateCurrentModelsAndDisplayDropdown);
          }
        });

        function updateCurrentModelsAndDisplayDropdown(data) {
          if (!_.isUndefined(data)) {
            $scope.currentModels = data;
            if (_.isUndefined($scope.modelSelected)) {
              $scope.modelSelected = false;
            }
            //TODO: This could be split out into something cleaner
            $scope.showDropdown = $scope.currentModels && !$scope.modelSelected && !_.isEmpty($scope.currentModels);
          } else {
            $scope.currentModels = null;
            $scope.showDropdown = false;
          }
        }

        $scope.selectedElement = 0;
        $scope.controlSelectedElementWithKeyboard = function(event) {
          var keyCode = event.keyCode;
          if ($scope.currentModels) {
            var lastIndexOfCurrentModels = $scope.currentModels.length - 1;
            switch (keyCode) {
              case 27: // ESC
                $scope.toggleDropdownStatus();
                break;
              case 40: // DOWN
                $scope.selectedElement = $scope.selectedElement === lastIndexOfCurrentModels ?
                  0 :
                  $scope.selectedElement + 1;
                break;
              case 38: // UP
                $scope.selectedElement = $scope.selectedElement === 0 ?
                  lastIndexOfCurrentModels :
                  $scope.selectedElement - 1;
                break;
              case 13: // RETURN
                $scope.setModelId($scope.currentModels[$scope.selectedElement]);
                $scope.selectedElement = 0;
                break;
              default:
                return;
            }
          }

        };

        // model is not always populated when the directive initializes. if this happens, $scope.search is not set to model[$scope.labelAttr]
        // Additionally, the dialog does not lock. In order to avoid this from happening, we watch model, and set model when it loads.
        // But! We only want this to happen on the first load, so, we unbind it after the data has been loaded.
        $scope.$watch('modelId', function(newValue) {
          if (_.has(newValue, 'id')) {
            newValue = newValue.id;
            $scope.modelId = newValue;
          }
          if (newValue) {
            service.one(newValue).get().then($scope.updateModel);
          } else {
            $scope.clear();
          }
        });

        //TODO: Change this name, it doesn't properly describe what it does
        $scope.setModelId = function(model) {
          if ($scope.modelId !== model.id) {
            $scope.modelId = model.id;
          } else {
            $scope.updateModel($scope.model);
          }
        };

        // TODO: This method is crap, change it to explicid enable/disable functionality.
        // Additionally, if no new value is selected, set values to previous incarnation
        $scope.toggleDropdownStatus = function() {
          $scope.showDropdown = false;
          $scope.selectedElement = 0;
          if (!_.isUndefined($scope.model)) {
            $scope.modelSelected = !$scope.modelSelected;
            $scope.search = $scope.model[$scope.labelAttr];
          } else {
            $scope.modelSelected = false;
            $scope.search = '';
          }
        };

        $scope.updateModel = function(model) {
          $scope.model = model;
          $scope.search = model[$scope.labelAttr];
          if ($scope.currentModels) {
            $scope.currentModels = null;
          }
          $scope.modelSelected = true;
          $scope.showDropdown = false;
          $scope.selectedElement = 0;
        };

        $scope.clear = function() {
          $scope.model = null;
          $scope.modelSelected = false;
          $scope.search = '';
          $scope.modelId = null;
        };
      }
    };
  });
